import React from 'react';
import PropTypes from 'prop-types';
import style from './heading.css';

class Heading extends React.Component {
  constructor(props) {
    super(props);

    this.state = { show: props.show };

    this.handleTextFamily = this.handleTextFamily.bind(this);
    this.handleHideNotification = this.handleHideNotification.bind(this);
  }

  handleTextFamily() {
    fetch('/api/shoppingList/notifyFamily', {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        message:
          "I'm at a grocery store! Add to the " +
          'family shopping list if you would like anything!',
      }),
    })
      .then(response => response.json())
      .then(() => {
        this.setState({
          show: false,
        });
      });
  }

  handleHideNotification() {
    this.setState({
      show: false,
    });
  }

  render() {
    const notification = this.state.show ? (
      <div className={style.heading}>
        <div className={style.message}>
          You have arrived at a store. Would you like to notify your family?
        </div>
        <div className={style.buttonContainer}>
          <button
            className={style.button}
            onClick={this.handleTextFamily}
            type="button"
          >
            Yes
          </button>
        </div>
        <div className={style.buttonContainer}>
          <button
            className={style.button}
            onClick={this.handleHideNotification}
            type="button"
          >
            Ignore
          </button>
        </div>
      </div>
    ) : (
      <div />
    );

    return notification;
  }
}

Heading.propTypes = {
  show: PropTypes.bool,
};

export default Heading;
